<template lang="html">

  <div>

  <div class="mv-highlight">

    <transition name="fade">
      <div v-show="!playVideo" class="mv-highlight__image" :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }"></div>
    </transition>

    <div class="mv-highlight__overlay"></div>

    <transition name="fade">
      <iframe v-show="playVideo" id="mv-highlight__video" :src="movue.trailer" frameborder="0" allowfullscreen></iframe>
    </transition>

    <div class="mv-highlight__top">

      <div class="mv-highlight__meta">
        <div class="mv-highlight__meta__left">
          <p>2013</p>
          <img src="../../assets/media/vector/icons/seperator.svg" alt="" class="mv-highlight__score__icon">
          <p>{{ movue.genres }}</p>
        </div>

        <div class="mv-highlight__score">
          <img src="../../assets/media/vector/icons/fire.svg" alt="" class="mv-highlight__score__icon">
          <span class="mv-highlight__score__current">8.3</span>
          <div class="mv-highlight__score__seperator">/</div>
          <div class="mv-highlight__score__total">10</div>
        </div>
      </div>

      <div class="mv-highlight__user">
        <img src="https://avatars1.githubusercontent.com/u/8048514?s=460&v=4" alt="">
        <p>Martijn</p>
        <span>⌃</span>
      </div>

    </div>


    <div class="mv-highlight__middle">

      <h1> {{ movue.title }} </h1>

      <div
        @click="onLike"
        class="mv-highlight__fav">
        <img src="../../assets/media/vector/icons/heart.svg" alt="">
        <p>{{ userIsRegistered ? 'Verwijder van favorieten' : 'Voeg toe aan favorieten'}}</p>
      </div>

      <!-- <template v-if="userIsCreator">
        <app-edit-movue-dialog v-if="showEditModal" :movue="movue"></app-edit-movue-dialog>

        <button @click="showEditModal = true" class="mv-highlight__edit">
          <img src="../../assets/media/vector/icons/heart.svg" alt="">
          <p>Edit movue</p>
        </button>

      </template> -->

    </div>


    <div class="mv-highlight__bottom">

      <button @click="showVideo($event)" class="btn btn--secondary btn--icon">
        <img src="../../assets/media/vector/icons/camera.svg" alt="">
        <span class="video-btn">Show trailer</span>
      </button>

      <div class="mv-highlight__slider"></div>

    </div>
  </div>

  <div class="mv-quote">
    <h2>{{ movue.quote }}</h2>
    <div class="mv-quote__right">
      <p>{{ movue.description }}</p>
    </div>
  </div>

  <div class="mv-bg-image" :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }">
    <div class="mv-bg-image__gr-top"></div>
    <div class="mv-bg-image__gr-ov"></div>
    <div class="mv-bg-image__gr-bottom"></div>
  </div>

  <div class="mv-split">

    <div class="mv-split__left">
      <div class="mv-similair">
        <h2>Similair shows</h2>


        <div class="mv-similair__cards">
          <article
            v-for="movue in movues"
            class="movue-card movue-card--small"
            :style="{ backgroundImage: 'url(' + movue.posterImage + ')' }">

            <router-link :to="'/movues/' + movue.id" router>
              <section class="movue-card__top">
                {{ movue.genre }}
              </section>

              <section class="movue-card__bottom">
                <h3 class="movue-card__title"> {{ movue.title }} </h3>
              </section>
            </router-link>
          </article>

        </div>

      </div>

      <div class="mv-actors">
        <h2>Actors</h2>

        <div class="mv-actors__cards">

          <section class="mv-card--mini" v-for="actor in actors">
            <div class="mv-actors__img" :style="{ backgroundImage: 'url(' + actor.url + ')' }"></div>
            <h4>{{ actor.actor }}</h4>
            <small>{{ actor.played }}</small>
          </section>


        </div>

      </div>

    </div>

    <div class="mv-split__right">

      <div class="mv-reviews">
        <h2>Reviews</h2>

        <article class="mv-review" v-for="review in reviews">
          <div class="mv-review__score">
            <div class="mv-highlight__score">
              <img src="../../assets/media/vector/icons/fire.svg" alt="" class="mv-highlight__score__icon">
              <span class="mv-highlight__score__current">{{ review.score }}</span>
              <div class="mv-highlight__score__seperator">/</div>
              <div class="mv-highlight__score__total">10</div>
            </div>
          </div>
          <div class="mv-review__body">
            {{ review.review }}
          </div>

          <small class="mv-review__author">
            - {{ review.author }}
          </small>
        </article>

        <h2>Write a review</h2>

        <form @submit.prevent="submitReview">
          <input v-model="author" type="text" placeholder="Your name">
          <input v-model="score" type="number" placeholder="Score" min="0" max="10">
          <textarea v-model="review" name="name" rows="4" cols="80" placeholder="Your review"></textarea>

          <button type="submit" class="btn btn--secondary">Submit review</button>
        </form>

      </div>

    </div>


  </div>

  </div>
</template>

<script>

export default {
  props: ['id'],
  data () {
    return {
      showEditModal: false,
      score: '',
      review: '',
      author: '',
      playVideo: false,
      actors: [
          {
              url: '../src/assets/media/img/actors/morgan-freeman.jpg',
              actor: 'Morgan Freeman',
              played: 'Allan Trumbull'
          },
          {
              url: '../src/assets/media/img/actors/leo.jpeg',
              actor: 'Leonardo Dicaprio',
              played: 'Jim Stone'
          },
          {
              url: '../src/assets/media/img/actors/keanu.jpg',
              actor: 'Keanu Reeves',
              played: 'Peter Jones'
          },
      ],
      newReview: {
          score: '',
          review: '',
          author: ''
      },
      reviews: [
          {
              score: '8,3',
              review: 'While it was just a TV show, that little speech at the beginning of the original Star Trek show really did do a good job of capturing our feelings about space. It is those feelings that drive our love of astronomy and our desire to learn more and more about it.',
              author: 'Jimmy'
          },
          {
              score: '6,4',
              review: 'While it was just a TV show, that little speech at the beginning of the original Star Trek show really did do a good job of capturing our feelings about space. It is those feelings that drive our love of astronomy and our desire to learn more and more about it.',
              author: 'Jimmy'
          }
      ]
    }
  },
  computed: {
    movue () {
      return this.$store.getters.loadedMovue(this.id)
    },
    userIsAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    },
    userIsCreator () {
      if (!this.userIsAuthenticated) {
        return false
      }
      return this.$store.getters.user.id === this.movue.creatorId
    },
    userIsRegistered () {
        return this.$store.getters.user.registeredMovues.findIndex(movueId => {
          return movueId === this.movue.id
        }) >= 0
    },
    movues () {
        var array = this.$store.getters.loadedMovues

        for (var i = array.length - 1; i > 0; i--) {
            var j = Math.floor(Math.random() * (i + 1));
            var temp = array[i];
            array[i] = array[j];
            array[j] = temp;
        }

        var slice = array.slice(0, 2);

        return slice
    }
  },
  methods: {
    onLike () {
      if (this.userIsRegistered) {
        this.$store.dispatch('unregisterUserFromMovue', this.movue.id)
      } else {
        this.$store.dispatch('registerUserForMovue', this.movue.id)
      }
    },
    showVideo () {
        var btnText = document.querySelector('.video-btn');
        var videoContainer = document.querySelector('.mv-highlight');
        var videoFrame = document.querySelector('#mv-highlight__video');

        if(!this.playVideo){
            btnText.innerHTML = 'Hide trailer';
            videoContainer.classList.add('video-playing');

        } else {
            btnText.innerHTML = 'Show trailer';
            videoContainer.classList.remove('video-playing');
        }

        this.playVideo = !this.playVideo;

    },
    submitReview () {
        this.newReview.score = this.score
        this.newReview.author = this.author
        this.newReview.review = this.review
        this.reviews.push(this.newReview)
        this.newReview = {}
    }
  }
}
</script>







