<template lang="html">
  <div class="mv-reviews add-movue-container">
    <h1>Add a movue</h1>

    <div class="form-preview-container">
    <form @submit.prevent="onCreateMovue">
      <label for="title">Title</label>
      <input v-model="title" name="title" type="text" placeholder="Title">

      <label for="image-url">Image url</label>
      <button @click="onPickFile" class="btn btn--secondary">Upload image</button>
      <input
      ref="fileInput"
      type="file"
      style="display: none"
      accept="image/*"
      @change="onFilePicked"
      >

      <label for="genre">Genre</label>
      <select v-model="genres">
          <option value="action">Action</option>
          <option value="drama">Drama</option>
          <option value="comedy">Comedy</option>
          <option value="horror">Horror</option>
          <option value="historical">Historical</option>
      </select>

      <label for="genre">Quote</label>
      <textarea v-model="quote" type="text"></textarea>

      <label for="description">Description</label>
      <textarea v-model="description" name="description" placeholder="Description"></textarea>

      <label for="description">Trailer</label>
      <input v-model="trailer" name="description" type="text" placeholder="Youtube link">

      <label>Highlight movue</label>
      <div class="radio-container"> <input v-model="highlight" type="radio" name="highlight" value="yes"> <span>Yes</span> </div>
      <div class="radio-container"> <input v-model="highlight" type="radio" name="highlight" value="no"> <span>No</span> </div>

      <button type="submit" :disabled="!formIsValid" class="btn">Submit form</button>

    </form>

    <div class="add-movue-preview">

      <article
        class="movue-card"
        :style="{ backgroundImage: 'url(' + posterImage + ')' }">

          <section class="movue-card__top">
            {{ genres }}
          </section>

          <section class="movue-card__bottom">
            <h3 class="movue-card__title"> {{ title }} </h3>
          </section>

      </article>

      <div class="mv-quote">
        <h2>{{ quote }}</h2>
        <div class="mv-quote__right">
          <p>{{ description }}</p>
        </div>
      </div>

      </div>

    </div>


  </div>
</template>

<script>
export default {
  data () {
    return {
      title: 'Title of the movue',
      posterImage: 'http://www.mormontransitions.org/wp-content/uploads/2015/12/Movie-making4.jpg',
      description: '',
      genres: 'Action',
      quote: '',
      image: null,
      highlight: '',
      trailer: ''
    }
  },
  computed: {
    formIsValid () {
      return this.title !== '' &&
      this.posterImage !== '' &&
      this.genres !== '' &&
      this.quote !== '' &&
      this.description !== ''
    }
  },
  methods: {
    onCreateMovue () {
      if (!this.formIsValid) {
        return
      }
      if (!this.image) {
        return
      }
      const movueData = {
        title: this.title,
        image: this.image,
        description: this.description,
        quote: this.quote,
        genres: this.genres,
        trailer: this.trailer,
        highlight: this.highlight,
        date: new Date()
      }
      this.$store.dispatch('createMovue', movueData)
      this.$router.push('/')
    },
    onPickFile () {
      this.$refs.fileInput.click()
    },
    onFilePicked (event) {
      const files = event.target.files
      let filename = files[0].name
      if (filename.lastIndexOf('.') <= 0) {
        return alert('Please add a valid file')
      }
      const fileReader = new FileReader()
      fileReader.addEventListener('load', () => {
        this.posterImage = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]
    }
  }
}
</script>

<style lang="scss">
.add-movue-container{
    padding: 40px;

    .btn[type=submit]{
        margin-top: 20px;
    }
}

.radio-container {
    display: flex;
    align-items: center;

    input{
        margin: 0;
        margin-right: 10px;
    }
}

.movue-card{
  position: relative;

  &__bottom{
    position: absolute;
    width: 100%;
    bottom: 0;
  }
}

.form-preview-container{
  display: flex;
}

form{
  width: 350px;
  margin-right: 50px;
}

.add-movue-preview{
  max-width: 330px;

  h2{
    font-size: 3.3rem;
  }
}
</style>



